import { defineStore } from 'pinia'

const productData=  JSON.parse(localStorage.getItem('productData'))
const cartProductlist = JSON.parse(localStorage.getItem('cartProductlist'));
export const useProductStore = defineStore('product', {
    state: () => {
        return {
            data: productData,
            cartProductlist: cartProductlist ? cartProductlist : [],
            currentProduct: [],
        }
    },
    getters: {
        count_length() {
            return this.cartProductlist.length
        },
        subtotalFun() {
            return this.cartProductlist.reduce((acc, item) => acc + item.total * 1, 0);
        },
    },
    actions: {
        getcurrentProduct(id) {
            return this.currentProduct = this.data.filter((product) => product.id == id)
        },
        addtocart(id) {
            this.cartProductlist.push(this.data.filter((product) => product.id == id)[0]);
            localStorage.setItem('cartProductlist', JSON.stringify(this.cartProductlist))
        },
        removecartproduct(index) {
            this.cartProductlist.splice(index, 1);
            localStorage.setItem('cartProductlist', JSON.stringify(this.cartProductlist))
        },
    }
})